"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";

const PET_TYPES = [
  { id: "dog", label: "Dogs", icon: "üê∂" },
  { id: "cat", label: "Cats", icon: "üê±" },
];

const GENDERS = ["Any", "Male", "Female"] as const;
const SIZES = ["Small", "Medium", "Large"] as const;
const AGES = ["Baby", "Young", "Adult", "Senior"] as const;

type ToggleValue<T extends readonly string[]> = T[number] | null;

function ToggleChip<T extends readonly string[] | { id: string }[]>(props: {
  selected: string | null;
  onChange: (val: string | null) => void;
  items: T;
  getId?: (item: T[number]) => string;
  render: (item: T[number], isActive: boolean) => React.ReactNode;
}) {
  const { selected, onChange, items, getId = (x: any) => x } = props;

  return (
    <div className="flex flex-wrap gap-2">
      {items.map((item) => {
        const id = getId(item);
        const active = id === selected;

        return (
          <button
            key={id}
            onClick={() => onChange(active ? null : id)}
            className={[
              "rounded-full border px-4 py-1 text-sm transition",
              active
                ? "bg-orange-500 text-white border-orange-500"
                : "bg-white text-gray-800 border-gray-300 hover:bg-gray-100",
            ].join(" ")}
          >
            {props.render(item, active)}
          </button>
        );
      })}
    </div>
  );
}

export default function SearchPage() {
  const router = useRouter();

  const [petType, setPetType] = useState<ToggleValue<typeof PET_TYPES>>(null);
  const [gender, setGender] = useState<ToggleValue<typeof GENDERS>>(null);
  const [size, setSize] = useState<ToggleValue<typeof SIZES>>(null);
  const [age, setAge] = useState<ToggleValue<typeof AGES>>(null);

  const handleSubmit = () => {
    const params = new URLSearchParams();
    if (petType) params.set("type", petType);
    if (gender) params.set("gender", gender);
    if (size) params.set("size", size);
    if (age) params.set("age", age);

    router.push(`/search?${params.toString()}`);
  };

  return (
    <div className="p-6 space-y-8">
      <h1 className="text-2xl font-semibold">Pet Search</h1>

      <section className="space-y-3">
        <h2 className="text-lg font-medium">Pet Types</h2>

        <ToggleChip
          selected={petType}
          onChange={setPetType}
          items={PET_TYPES}
          getId={(p) => p.id}
          render={(p) => (
            <span className="flex items-center gap-1">
              <span>{p.icon}</span>
              <span>{p.label}</span>
            </span>
          )}
        />
      </section>

      <section className="space-y-3">
        <h2 className="text-lg font-medium">
          Gender <span className="text-sm text-gray-400">(Optional)</span>
        </h2>

        <ToggleChip
          selected={gender}
          onChange={setGender}
          items={GENDERS}
          render={(g) => g}
        />
      </section>

      {/* Size */}
      <section className="space-y-3">
        <h2 className="text-lg font-medium">
          Size <span className="text-sm text-gray-400">(Optional)</span>
        </h2>

        <ToggleChip
          selected={size}
          onChange={setSize}
          items={SIZES}
          render={(s) => s}
        />
      </section>

      {/* Age */}
      <section className="space-y-3">
        <h2 className="text-lg font-medium">
          Age <span className="text-sm text-gray-400">(Optional)</span>
        </h2>

        <ToggleChip
          selected={age}
          onChange={setAge}
          items={AGES}
          render={(a) => a}
        />
      </section>

      {/* Search button */}
      <div className="pt-4">
        <button
          onClick={handleSubmit}
          className="w-full rounded-full bg-orange-500 py-3 text-center text-white font-medium shadow hover:bg-orange-600 transition"
        >
          Search
        </button>
      </div>
    </div>
  );
}
